<template>
  <div class="login">
    <router-link to="/"
      ><img
        src="https://img2.baidu.com/it/u=990202345,2924410068&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=320"
        class="logo"
        alt=""
    /></router-link>
    <van-form @submit="onSubmit">
      <van-field
        v-model="userName"
        name="userName"
        label="用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
      <div style="margin: 16px">
        <van-button round block type="info" native-type="submit"
          >登录</van-button
        >
      </div>
    </van-form>
    <router-link :to="{ name: 'Reg' }">没有账号，我要注册</router-link>
  </div>
</template>

<script>
import { Notify } from "vant";
import { loginAPI } from "../services/auth";
import { setToken } from "../utils/auth";
export default {
  data() {
    return {
      userName: "",
      password: "",
    };
  },
  methods: {
    async onSubmit() {
      const res = await loginAPI({
        userName: this.userName,
        password: this.password,
      });
      if (res.code == 1) {
        Notify({
          message: "登录成功",
          type: "success",
        });
        setToken(res.data);
        this.$router.push({
            name:"User"
        })
        // 派发action获取购物车信息
        this.$store.dispatch("loadCarts");
        this.$router.replace({
    
          name: "User",
        });
      } else {
        Notify({
          message: res.data,
          type: "warning",
        });
      }
    },
  },
};
</script>

<style scoped>
.logo {
  width: 45vw;
  display: block;
  margin: 20px auto;
  border-radius: 16px;
}
</style>
